import React, { Component } from 'react';
import PropTypes from 'prop-types';

export class Search extends Component {
  state = {
    text: '',
  };
  static propTypes = {
    searchUsers: PropTypes.func.isRequired,
    clearUsers: PropTypes.func.isRequired,
    showClear: PropTypes.bool.isRequired,
    setAlert: PropTypes.func.isRequired,
  };
  onChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };
  onSubmit = (e) => {
    e.preventDefault();
    if (this.state.text === '') {
      this.props.setAlert('Enter Something to search', 'danger');
    } else {
      this.props.setAlert(this.setState({ alert: null }));
      this.props.setAlert(
        'Successfully fatched the result from github!!',
        'success'
      );
      this.props.searchUsers(this.state.text);
      this.setState({ text: '' });
    }
  };
  render() {
    return (
      <div>
        <form onSubmit={this.onSubmit} className='row g-3'>
          <div class='col-md-5'>
            <input
              type='text'
              name='text'
              placeholder='Search Users..'
              className='form-control'
              value={this.state.text}
              onChange={this.onChange}
            />
          </div>
          <div class='col-auto'>
            <input type='submit' value='Search' className='btn btn-dark mb-3' />
          </div>
          <div class='col-auto'>
            {this.props.showClear && (
              <button
                className='btn btn-warning mb-3'
                onClick={this.props.clearUsers}
              >
                Clear
              </button>
            )}
          </div>
        </form>
        <hr />
      </div>
    );
  }
}

export default Search;
