import React, { Component, Fragment } from 'react';
import Spinner from '../layout/Spinner';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Repos from '../repos/Repos';

export class User extends Component {
  componentDidMount() {
    //console.log(this.props.match.params.login);
    this.props.getUser(this.props.match.params.login);
    this.props.getUserRepos(this.props.match.params.login);
  }
  static propTypes = {
    loading: PropTypes.bool,
    user: PropTypes.object.isRequired,
    getUser: PropTypes.func.isRequired,
    repos: PropTypes.array.isRequired,
    getUserRepos: PropTypes.func.isRequired,
  };
  render() {
    const {
      name,
      avatar_url,
      location,
      followers,
      following,
      bio,
      blog,
      login,
      html_url,
      company,
      public_repos,
      public_gists,
      hireable,
    } = this.props.user;
    if (loading) return <Spinner />;
    const { loading, repos } = this.props;
    return (
      <Fragment>
        <a href='/' className='btn btn-secondary'>
          <i class='fa fa-arrow-left' aria-hidden='true'></i> Back to Home
        </a>
        <br />
        Hirable:{''}
        {hireable ? 'Yes' : 'No'}
        <div className='card mb-3' style={{ width: '50rem' }}>
          <div class='row g-0'>
            <div class='col-md-4'>
              <img
                src={avatar_url}
                alt={login}
                className='card-img-center rounded'
                style={{ width: '200px', margin: '7px 10px' }}
              />
            </div>
            <div class='col-md-8'>
              <div class='card-body'>
                <h5 class='card-title'>{name}</h5>
                <p className='card-text'>{location}</p>

                {bio && (
                  <p className='card-text'>
                    <h4>Bio:</h4>
                    {bio}
                  </p>
                )}
                {company && (
                  <p className='card-text'>
                    <h5>Company:</h5>
                    {company}
                  </p>
                )}
                {blog && (
                  <p className='card-text'>
                    <h5>Website:</h5>
                    {blog}
                  </p>
                )}
                <a href={html_url} class='btn btn-danger'>
                  Visit on Github
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className='card text-center' style={{ width: '50rem' }}>
          <div className='card-body row'>
            <div class='col-md-3 lead'>
              Followers:<span class='badge bg-warning'>{followers}</span>
            </div>
            <div class='col-md-3 lead'>
              Followings:<span class='badge bg-dark'>{following}</span>
            </div>
            <div class='col-md-3 lead'>
              Public Repos:<span class='badge bg-info'>{public_repos}</span>
            </div>
            <div class='col-md-3 lead'>
              Public Gist:<span class='badge bg-danger'>{public_gists}</span>
            </div>
          </div>
        </div>
        <hr />
        <p className='h2 alert alert-info'>Last 12 Repository </p>
        <div className='row'>
          <Repos repos={repos} />
        </div>
      </Fragment>
    );
  }
}

export default User;
